<template>
  <div class="container">
    <!-- transitionvue -->
    <transition name="dialog-fade">
      <!--  -->
      <!-- .self -->
      <div class="lys-dialog_wrapper" v-show="visible" @click.self="handleClose">
        <div class="lys-dialog" :style="{width, marginTop: top}">
          <!--       -->
          <div class="lys-dialog_header">
            <!-- title -->
            <slot name="title">
              <span class="lys-dialog_title">{{ title }}</span>
            </slot>
            <!--   -->
            <button class="lys-dialog_headerbtn" @click="handleClose">
              <i class="lys-icon-close"></i>
            </button>
          </div>
          <!--      -->
          <div class="lys-dialog_body">
            <slot><span></span></slot>
          </div>
          <!--       -->
          <div class="lys-dialog_footer" v-if="$slots.footer">
            <slot name="footer"></slot>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'LysDialog',
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    width: {
      type: String,
      default: '50%'
    },
    top: {
      type: String,
      default: '15vh'
    },
    title: {
      type: String,
      default: ''
    }
  },
  methods: {
    handleClose () {
      // this.visible = false
      // update: sync,update
      // .sync:   
      this.$emit('update:visible', false)
    }
  }
}
</script>

<style scoped lang="scss">
@import './index.scss';
</style>
